.container{
  max-width: 1140px;
  margin: 0 auto;
}
.row{
    @include mobile{
        padding: 0 15px;
    }
}
.header{
  background: $primary;
  padding: 0 5%;
  @include mobile{
      padding: 0;
  }
  &Box {
      display: flex;
      justify-content: space-between;
      padding: 0;
      @include mobile{
        display: block;
    }
  }
  &Block{
      display: flex;
      justify-content: space-between;
      align-items: center; 
      @include mobile{
          display: block;
      }
  }
  &Logo{
      background: url(../images/logo.png) no-repeat center;
      background-size: contain;
      width: 80px;
      text-indent: 101%;
      white-space: nowrap;
      overflow: hidden;
      padding: 30px 0;
      &:hover{
        color: $secondary;
          opacity: 0.5;
          transition: all .3s;
      }
      @include mobile{
          width: 48px;
          margin: 0 auto;
          padding: 15px 0;
      }
  }
  &Menu{
      display: flex;
      justify-content: space-between;
      align-items: center;
      @include mobile{
          flex-wrap: wrap;
          justify-content: center;
      }
  }
  &Item{
    a{
        color: $white;
        font-size: $text-m;
        line-height: 1.5;
        padding: 30px 32px;
        @include mobile{
            padding: 8px 32px;
        }
    }
      @include mobile{
          width: 50%;
          border: 1px solid #fff;
      }
  }
  &Link{
    color: $white;
    font-size: $text-m;
    line-height: 1.5;
    padding: 30px 32px;
    &:hover{
        opacity: 0.5;
        transition: all .4s;
    }
    @include pad{
        padding: 30px 20px;
    }
    @include mobile{
        text-align: center;
        padding: 8px 0;
    }
}
}

.footer{
  background: $primary;
  color: $white;
  padding-top: 44px;
  padding-bottom: 39px;
  &Block{
      display: flex;
      justify-content: space-between;
      position: relative;
      margin-bottom: 32px;
      @include pad{
          margin-bottom: 24px;
      }
      @include mobile{
          margin-bottom: 16px;
      }
      &::after{
          position: absolute;
          content: "";
          width: 100%;
          border-bottom: 1px solid #fff;
          bottom: 0;
      }
  }
  &Nav{
      width: 60%;
      @include pad{
          width: 65%;
      }
  }
  &Menu{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 16px;
      @include pad{
          margin-top: 38px;
          margin-bottom: 18px;
      }
      @include mobile{
          display: none;
      }
  }
  &Link{
      font-size: $text-m;
      line-height: 1.5;
      color: $white;
      @include pad{
          font-size: $text-base;
      }
      &:hover{
         color: $secondary;
          opacity: 0.5;
          transition: all .4s;
      }
  }
}

.footer{
    @include pad{
        padding: 0 39px;
     }
     @include mobile{
        padding: 0 15px;
        padding-top: 25px;
        padding-bottom: 24px;
    }
  &Contact{
      padding-bottom: 16px;

      @include pad{
         padding-bottom: 18px;
      }
      @include mobile{
          padding-bottom: 21px;
      }
  }
  &ContactLink{
      font-size: $text-2xl;
      line-height: 1.5;
      color: $white;
      display: flex;
      align-items: center;
      padding: 8px 0;
      &:hover{
        color: $secondary;
         opacity: 0.5;
         transition: all .4s;
     }
      @include pad{
          font-size: $text-base;
          padding: 6px 0;
      }
      @include mobile{
          padding: 3px 0;
      }
  }
  &ContactPic{
      margin-right: 20px;
      width: 48px;
      @include pad{
          margin-right: 10px;
          width: 19px;
      }
  }
}

.social{
  &Menu{
      display: flex;
      justify-content: space-between;  
  }
  &Link{
      padding: 10px 20px;
      @include pad{
        margin-top: 0px;
        padding: 0px 12px;
    }
      @include mobile{
          padding: 0 10px;
      }
      &:hover{
          opacity: 0.5;
          transition: all .4s;
          color: $secondary;
      }
  }
  &Icon{
      @include pad{
          width: 24px;
        //   margin-top: 32px;
      }
  }
}

.copyright{
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mobile{
      display: block;
  }
  &Text{
      font-size: $text-base;
      line-height: 1.5;
      @include pad{
        padding-bottom: 32px
      }
      @include mobile{
          font-size: $text-s;
          padding-bottom: 8px;
      }
  }
}


.privacy{
  &Menu{
      width: 17%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @include pad{
          width: 25%;
      }
      @include mobile{
          display: block;
      }
  }
  &Item{
      @include mobile{
          margin-bottom: 8px;
      }
  }
  &Link{
      color: $white;
      font-size: $text-s;
      line-height: 1.5;
      &:hover{
          color: $secondary;
          opacity: 0.5;
          transition: all .4s;
      }
  }
}